.back {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  left: 10px;
  top: 10px;
  width: 70px;
  height: 70px;
  border-radius: 50%;
  cursor: pointer;
  background: #fff;
  border: 1px solid #cbcbcb;
  transition: all 0.3s ease;
}

.back:hover {
  border: 1px solid #000;
}

.button {
  cursor: pointer;
  font-size: 4rem;
  text-transform: uppercase;
  font-weight: 600;
  background: transparent;
  padding-bottom: 10px;
}
