.parent {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 0.5fr;
  grid-column-gap: 0px;
  grid-row-gap: 20px;
  padding: 120px 0 90px;
}

.heading {
  text-align: center;
  font-size: 9.375rem;
  font-weight: 900;
  text-transform: uppercase;
  line-height: 100%;
  grid-area: 1 / 1 / 2 / 4;
}

.mainTopPic {
  margin-left: 10px;
}

.subHeading {
  font-size: 1rem;
  grid-area: 2 / 1 / 3 / 2;
}
