.parent {
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-template-rows: 2fr 1fr;
  grid-column-gap: 140px;
  grid-row-gap: 0px;
  padding: 100px 0 60px;
}

.sultanImageContainer {
  position: relative;
  margin-bottom: 5px;
}

.sultanImage {
  max-width: 420px;
  grid-area: 1 / 1 / 2 / 2;
}

.flagImage {
  position: absolute;
  left: 10px;
  bottom: 40px;
}

.mapImage {
  grid-area: 1 / 2 / 2 / 3;
  margin-bottom: 35px;
}

.sultanText {
  grid-area: 2 / 1 / 3 / 2;
}

.sultanText h2 {
  margin-bottom: 10px;
}

.mapText {
  grid-area: 2 / 2 / 3 / 3;
}
