* {
  margin: 0;
  padding: 0;
  text-decoration: none;
  border: none;
  color: inherit;
  box-sizing: border-box;
  list-style: none;
  font-family: 'Montserrat', sans-serif;
}

body {
  overflow-x: hidden;
  background: #f4f4f4;
  /* background-image: url('../images/map-bg-main.svg'); */
  background-repeat: no-repeat;
  background-size: contain;
  font-size: 1.25rem;
  line-height: 100%;
}

@font-face {
  font-family: 'Montserrat';
  src: local('Montserrat-Regular'), url('../fonts/Montserrat-Regular.woff') format('woff2'),
    url('../fonts/Montserrat-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Montserrat';
  src: local('Montserrat-SemiBold'), url('../fonts/Montserrat-SemiBold.woff') format('woff2'),
    url('../fonts/Montserrat-SemiBold.woff') format('woff');
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Montserrat';
  src: local('Montserrat-Black'), url('../fonts/Montserrat-Black.woff') format('woff2'),
    url('../fonts/Montserrat-Black.woff') format('woff');
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}

.container {
  max-width: 1300px;
  margin: 0 auto;
}
