.map {
  background: #ebebeb;
  border: 1px solid #cbcbcb;
  margin-bottom: 50px;
  position: relative;
}

.map path {
  cursor: pointer;
  stroke-width: 0.3;
  stroke: #000000;
  fill: #ffffff;
  transition: fill 0.5s;
}

.map path:hover {
  fill: #92ba8c;
}

.map path.selected {
  fill: #92ba8c;
}
