.countryCard {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  text-align: center;
  width: 300px;
  height: 450px;
  background: #92ba8c;
  position: absolute;
  top: 0;
  padding: 10px;
}

.countryInfo {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.countryName {
  font-size: 1.5rem;
  text-transform: uppercase;
  margin-bottom: 15px;
}

.bigFlag {
  width: 159px;
  height: 107px;
  margin-bottom: 15px;
}

.years {
  display: flex;
  align-items: center;
  justify-content: center;
}

.littleFlag {
  width: 38px;
  height: 25px;
  margin-right: 5px;
  background: #000;
}

.yearsText {
  font-size: 1rem;
}

.button {
  margin-top: 15px;
  background: #fff;
  width: 200px;
  height: 100px;
  border-radius: 5px;
  cursor: pointer;
  transition: scale 0.3s;
}

.button:hover {
  scale: 1.05;
}

.buttonText {
  font-size: 1rem;
}
