.parent {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(6, 1fr);
    grid-column-gap: 20px;
    grid-row-gap: 20px;
}

.parentEmpty {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: auto;
    grid-column-gap: 20px;
    grid-row-gap: 20px;

    .textAndPhoto_0 {
        margin-bottom: 200px;
    }

    .textAndPhoto_1 {
        margin-bottom: 100px;
    }
}

.photo_0 {
    grid-area: 1 / 1 / 3 / 4;
}

.textAndPhoto_0 {
    grid-area: 2 / 5 / 4 / 7;
}

.photo_1 {
    grid-area: 1 / 4 / 3 / 7;
}

.textAndPhoto_1 {
    grid-area: 2 / 1 / 4 / 3;
}

.texts {
    margin-bottom: 20px;
}

.name {
    line-height: 3rem;
    font-weight: 600;
    font-size: 2rem;
}

.text {
    display: flex;
    font-size: 2rem;
    line-height: 3rem;
}

.textBold {
    line-height: 3rem;
    font-weight: 600;
    font-size: 2rem;
    margin-left: 20px;
}

.photos {
    grid-area: 4 / 1 / 7 / 7;
}

.photos img{
    max-width: 420px;
}

.photos img:not(:last-child) {
    margin-right: 20px;
}

.photos img:hover {
    cursor: pointer;
}

.modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
}

.enlargedImage {
    scale: 1.75;
}