.parent {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(2, 1fr) repeat(2, 0.5fr) repeat(4, 1fr);
  grid-column-gap: 20px;
  grid-row-gap: 20px;
  margin-bottom: 200px;
}

.parent p {
  font-size: 1.25rem;
}

.left {
  grid-area: 1 / 1 / 5 / 4;
}

.littlePhoto {
  margin-right: 20px;
  margin-bottom: 20px;
}

.bigPhoto {
  margin-bottom: 20px;
}

.text1 {
  margin-bottom: 20px;
}

.text2 {
}

.right {
  grid-area: 5 / 4 / 9 / 7;
}

.text3 {
  margin-bottom: 20px;
}

.hugePhoto {
  margin-bottom: 20px;
}

.text4 {
}
