.parent {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 20px;
  grid-row-gap: 0px;
  padding: 100px 0;
}

.left {
  grid-area: 1 / 1 / 2 / 2;
}

.left p {
  margin-bottom: 20px;
}

.right {
  grid-area: 1 / 2 / 2 / 3;
}

.right img {
  margin-bottom: 20px;
}
