.yearButtons {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;
}

.yearArrowPrev {
  padding: 15px;
  border: 1px solid #cbcbcb;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.3s ease;
}

.yearArrowPrev:hover {
  border: 1px solid #000;
}

.currentYear {
  margin: 0 20px;
  font-size: 1.25rem;
  font-weight: 600;
}

.yearArrowNext {
  padding: 15px;
  border: 1px solid #cbcbcb;
  border-radius: 50%;
  cursor: pointer;
  rotate: 180deg;
  transition: all 0.3s ease;
}

.yearArrowNext:hover {
  border: 1px solid #000;
}

.timeline {
  display: flex;
  justify-content: space-between;
  padding: 18px 0;
  border-bottom: 1px solid #cbcbcb;
  border-top: 1px solid #cbcbcb;
  margin-bottom: 90px;
}

.yearButton {
  font-size: 1.5rem;
  color: #646464;
  background: transparent;
  cursor: pointer;
  transition: all 0.3s ease;
}

.yearButton:hover {
  scale: 1.1;
}

.yearButtonActive {
  font-size: 1.5rem;
  scale: 1.1;
  font-weight: 600;
  background: transparent;
  color: #000;
  cursor: pointer;
  transition: all 0.3s ease;
}
